import { myRequestGet } from '@/utils/request.js';
export default {
	data() {
		return {
			video1: [],
			article1: [],
			video2: [],
			article2: [],
			video3: [],
			article3: []
		};
	},
	created() {
		this.getVideos1();
		this.getArticle1();
		this.getVideos2();
		this.getArticle2();
		this.getVideos3();
		this.getArticle3();
	},
	methods: {
		async getVideos1() {
			let result = await myRequestGet('/api/v1/fatiao/index/list', {
				id: 26,
				p: 1,
				order: 1,
				limit: 1,
				channel_id: 26
			});
			console.log(result);
			this.video1 = result;
		},
		async getArticle1() {
			let result = await myRequestGet('/api/v1/fatiao/article/list', {
				id: 26,
				p: 1,
				order: 1,
				limit: 6,
				channel_id: 26
			});
			console.log(result);
			this.article1 = result;
		},
		async getVideos2() {
			let result = await myRequestGet('/api/v1/fatiao/index/list', {
				id: 26,
				p: 2,
				order: 1,
				limit: 1,
				channel_id: 26
			});
			console.log(result);
			this.video2 = result;
		},
		async getArticle2() {
			let result = await myRequestGet('/api/v1/fatiao/article/list', {
				id: 26,
				p: 2,
				order: 1,
				limit: 6,
				channel_id: 26
			});
			console.log(result);
			this.article2 = result;
		},
		async getVideos3() {
			let result = await myRequestGet('/api/v1/fatiao/index/list', {
				id: 26,
				p: 3,
				order: 1,
				limit: 1,
				channel_id: 26
			});
			console.log(result);
			this.video3 = result;
		},
		async getArticle3() {
			let result = await myRequestGet('/api/v1/fatiao/article/list', {
				id: 26,
				p: 3,
				order: 1,
				limit: 6,
				channel_id: 26
			});
			console.log(result);
			this.article3 = result;
		}
	}
};
